/* Global Styles */
:root {
  --primary-blue: #007bff;
  --primary-hover: #0056b3;
  --bg-dark: #0a0f18;
  --card-bg: rgba(13, 20, 33, 0.9);
  --text-primary: #ffffff;
  --text-secondary: #b8c2cc;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif;
  background-color: var(--bg-dark);
  color: var(--text-primary);
}

.App {
  text-align: center;
  padding: 40px;
  max-width: 1400px;
  margin: 0 auto;
}

.progress-container {
  margin-top: 10px;
  margin-bottom: 10px;
}

h1 {
  font-size: 2.5rem;
  color: var(--primary-blue);
  text-shadow: 0 0 20px rgba(0, 123, 255, 0.3);
}

/* Search Component */
.search-container {
  position: relative;
  width: 400px;
  margin: 0 auto;
}

input[type="text"],
select,
textarea {
  padding: 10px;
  width: 100%;
  font-size: 16px;
  border: 1px solid rgba(0, 123, 255, 0.2);
  border-radius: 4px;
  background-color: rgba(13, 20, 33, 0.8);
  color: var(--text-primary);
  height: 48px;
  box-sizing: border-box;
  backdrop-filter: blur(10px);
  transition: all 0.3s ease;
}

input[type="text"]:focus,
select:focus,
textarea:focus {
  outline: none;
  border-color: var(--primary-blue);
  box-shadow: 0 0 10px rgba(0, 123, 255, 0.2);
}

button {
  padding: 10px 20px;
  height: 48px;
  font-size: 16px;
  border: none;
  border-radius: 4px;
  background-color: var(--primary-blue);
  color: var(--text-primary);
  cursor: pointer;
  transition: all 0.3s ease;
}

button:hover {
  background-color: var(--primary-hover);
  box-shadow: 0 0 15px rgba(0, 123, 255, 0.4);
}

/* Dropdown */
.dropdown {
  position: absolute;
  top: 45px;
  width: 100%;
  background-color: var(--card-bg);
  border: 1px solid rgba(0, 123, 255, 0.2);
  border-radius: 4px;
  max-height: 300px;
  overflow-y: auto;
  z-index: 10;
  backdrop-filter: blur(15px);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
}

.dropdown li {
  display: flex;
  align-items: center;
  padding: 10px;
  cursor: pointer;
  transition: background-color 0.2s ease;
}

.dropdown li:hover {
  background-color: rgba(0, 123, 255, 0.1);
}

.dropdown-poster {
  width: 50px;
  height: 75px;
  border-radius: 4px;
}

/* Watchlist */
.Watchlist {
  margin-top: 10px;
}

.Watchlist h2 {
  color: var(--primary-blue);
  margin-bottom: 20px;
  text-shadow: 0 0 20px rgba(0, 123, 255, 0.3);
}

/* Grid Layout */
.WatchlistGrid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 40px;
  padding: 30px;
}

.Movie {
  background-color: var(--card-bg);
  border-radius: 8px;
  padding: 20px;
  position: relative;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  backdrop-filter: blur(10px);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
  transition: all 0.3s ease;
  margin-bottom: 20px;
}

.Movie:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 25px rgba(0, 123, 255, 0.2);
}

.Movie img {
  width: 100%;
  border-radius: 4px;
  margin-bottom: 15px;
}

.Movie h3 {
  font-size: 1.2rem;
  color: var(--text-primary);
}

/* Remove Button */
.remove-button {
  position: absolute;
  top: 10px;
  left: 10px;
  background: rgba(0, 123, 255, 0.3);
  color: var(--text-primary);
  width: 32px;
  height: 32px;
  border-radius: 50%;
  backdrop-filter: blur(5px);
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease;
  z-index: 1;
}

.remove-button:hover {
  background-color: var(--primary-blue);
}

a {
  color: var(--text-primary);
  text-decoration: none;
  background-color: var(--primary-blue);
  padding: 10px;
  border-radius: 5px;
  transition: all 0.3s ease;
}

a:hover {
  background-color: var(--primary-hover);
  box-shadow: 0 0 15px rgba(0, 123, 255, 0.4);
}

.movie-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  padding-top: 10px;
}

.movie-link {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  padding-top: 10px;
}